
'use client'; 

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import {
  SidebarProvider,
  Sidebar,
  SidebarHeader,
  SidebarContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  SidebarTrigger,
  SidebarSeparator,
  useSidebar,
} from '@/components/ui/sidebar';
import { LayoutDashboard, ListChecks, Star, Settings, MessageSquare, ShoppingCart, ShieldCheck } from 'lucide-react'; 
import React from 'react';
import { useAuth } from '@/context/auth-context';
import { BottomNavigation } from '@/components/layout/bottom-navigation';
import DesktopNav from '@/components/layout/desktop-nav';
import SiteLogoIcon from '@/components/icons/site-logo-icon';

const DashboardLayoutContent = ({ children }: { children: React.ReactNode }) => {
  const pathname = usePathname();
  const { firestoreUser } = useAuth();
  const { isMobile, setOpenMobile } = useSidebar();
  const roles = firestoreUser?.roles || [];

  const handleLinkClick = () => {
    if (isMobile) {
      setOpenMobile(false);
    }
  };

  const menuItems = [
    { href: '/dashboard', label: 'Оглядова панель', icon: <LayoutDashboard /> },
    { href: '/dashboard/lots', label: 'Мої лоти', icon: <ListChecks /> },
    { href: '/dashboard/sales', label: 'Мої продажі', icon: <ShoppingCart /> },
    { href: '/dashboard/messages', label: 'Повідомлення', icon: <MessageSquare /> },
    { href: '/dashboard/reviews', label: 'Відгуки', icon: <Star /> },
  ];

  const bottomMenuItems = [
     { href: '/dashboard/settings', label: 'Налаштування', icon: <Settings /> },
  ];

  const adminMenuItem = { 
    href: '/admin/dashboard', 
    label: 'Адмін-панель', 
    icon: <ShieldCheck className="text-red-600" />,
    className: 'text-red-600 hover:text-red-700 font-semibold'
  };

  return (
    <div className="flex min-h-screen bg-background">
      <Sidebar collapsible="icon" side="left" variant="sidebar">
        <SidebarHeader className="flex items-center justify-center border-b border-sidebar-border h-16">
            <Link href="/" className="text-primary hover:opacity-80 transition-opacity">
                <SiteLogoIcon className="h-12 w-12" />
            </Link>
        </SidebarHeader>
        <SidebarContent className="p-2 flex flex-col">
          <SidebarMenu className="flex-1">
            {menuItems.map((item) => {
                const isActive = item.href === '/dashboard' 
                    ? pathname === item.href 
                    : pathname.startsWith(item.href);

                return (
                  <SidebarMenuItem key={item.href}>
                    <SidebarMenuButton asChild isActive={isActive} tooltip={{ children: item.label, side: 'right' }}>
                      <Link href={item.href} className="flex items-center" onClick={handleLinkClick}>
                        {item.icon}
                        <span className="group-data-[collapsible=icon]:hidden ml-2">{item.label}</span>
                      </Link>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                );
            })}

            {roles.includes('admin') && (
              <>
                <SidebarSeparator className="my-2" />
                <SidebarMenuItem key={adminMenuItem.href}>
                  <SidebarMenuButton asChild isActive={pathname.startsWith(adminMenuItem.href)} tooltip={{ children: adminMenuItem.label, side: 'right' }} className={adminMenuItem.className}>
                    <Link href={adminMenuItem.href} className="flex items-center" onClick={handleLinkClick}>
                      {adminMenuItem.icon}
                      <span className="group-data-[collapsible=icon]:hidden ml-2">{adminMenuItem.label}</span>
                    </Link>
                  </SidebarMenuButton>
                </SidebarMenuItem>
              </>
            )}
          </SidebarMenu>
           <SidebarMenu>
             {bottomMenuItems.map((item) => (
              <SidebarMenuItem key={item.href}>
                <SidebarMenuButton asChild isActive={pathname.startsWith(item.href)} tooltip={{ children: item.label, side: 'right' }}>
                  <Link href={item.href} className="flex items-center" onClick={handleLinkClick}>
                    {item.icon}
                    <span className="group-data-[collapsible=icon]:hidden ml-2">{item.label}</span>
                  </Link>
                </SidebarMenuButton>
              </SidebarMenuItem>
            ))}
          </SidebarMenu>
        </SidebarContent>
      </Sidebar>
      
      <div className="flex-1 flex flex-col">
        <header className="sticky top-0 z-40 bg-background/80 backdrop-blur-md border-b border-border/50 shadow-sm">
            <div className="flex h-16 items-center justify-between px-4 sm:px-6 lg:px-8">
                <SidebarTrigger />
                <div className="hidden md:flex flex-1 items-center justify-end">
                    <DesktopNav />
                </div>
                <div className="md:hidden">
                    <Link href="/dashboard" className="flex items-center gap-2 text-foreground hover:opacity-80 transition-opacity">
                        <span className="font-headline text-lg font-semibold">Панель продавця</span>
                    </Link>
                </div>
            </div>
        </header>
        
        <main className="flex-1 p-4 sm:p-6 lg:p-8">
          {children}
        </main>
      </div>

      <BottomNavigation />
    </div>
  );
};

const DashboardLayout = ({ children }: { children: React.ReactNode }) => {
  return (
    <SidebarProvider>
      <DashboardLayoutContent>{children}</DashboardLayoutContent>
    </SidebarProvider>
  );
};

export default DashboardLayout;
